.relative{
    position: relative;
}
.logo{
    width: 80px;
    height: 40px;
    object-fit: cover;
    border-radius: 2rem;
}
ul{
    list-style-type:none;
  }
li a{
    text-decoration: none;
    text-transform: uppercase;
}
nav{
    padding: 15px 0;
    /* display: grid; */
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
nav li a{
    color: rgb(124, 124, 124) ;
    /* text-align: center; */
    font-size: 16px;
}
.active{
    border-bottom: 2.5px solid #4caf50;
}
.navLinks{
    padding-left: 0;
    width: 45%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin-bottom: 0; */
}
.navLinksMobile{
    display: none;
}
/* .navRight{
    width: 75%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
} */
nav button{
    padding: 2px 10px;
    border: none;
    border-radius: 2rem;
}
.toggleBtn{
    display: none;
}
.form{
    display: flex;
    align-items: center;
    padding-bottom: 5px;
}
.form input{
    height: 25px;
    width: 200px;
    border: 2px solid rgb(187, 187, 187);
    border-radius: 5px;
    margin-right: 10px;
}
.form button{
    background-color: white;
    padding: 0;
}
.seachBtn{
    display: none;
}
.search-mobile{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 5;
    visibility: hidden;
}
.search-flex{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.search-flex form{
    display: flex;
    align-items: center;
}
.search-flex form input{
    height: 25px;
    width: 250px;
    border: 2px solid rgb(187, 187, 187);
    border-radius: 5px;
    margin: 0 5px;
}
@media only screen and (max-width:1232px) {
    .relative{
        padding-left: 16px;
        padding-right: 16px;
    }
    nav{
        width: 100%;
    }
}
@media only screen and (max-width:1024px) {
    nav li a{
        font-size: 13px;
    }
}

@media only screen and (max-width:768px) {
    .navRight{
        width: 50%;
    }
    .seachBtn{
        display: block;
    }
    .toggleBtn{
        display: block;
    }
    .navLinks{
        display: none;
    }
    .form{
        display: none;
    }
    .navLinksMobile{
        display: block;
        background-color: rgb(50, 50, 50);
        position: fixed;
        flex-direction: column;
        justify-content: flex-start;
        top: 0;
        left: 0;
        z-index: 15;
        width: 30%;
        height: 100%;
        padding: 0 15px;
        margin-top: 0;
        transition: all 500ms;
    }
    .navLinksMobile li{
        padding: 20px 0;
    }
    .navLinksMobile a{
        color: white;
    }
    .search-mobile{
        visibility: visible;
    }
}
@media only screen and (max-width:600px) {
    .logo{
        width: 60px;
    }
    .form input{
        width: 120px;
    }
}
.items{
    position: relative;
}
.itemCategories{
    position: absolute;
    border-radius: 3px;
    width: 160%;
    opacity: 0;
}
.items:hover .itemCategories{
    height: 150px;
    opacity: 1;
    transition: opacity 500ms;
    z-index: 2;
}
